 <template>
  <div>
    <breadcrumb-wrapper>
      <template v-slot:left>
          <el-breadcrumb class="app-breadcrumb" separator="/" >
            <transition-group name="breadcrumb">
              <el-breadcrumb-item key="2" >
                 <router-link  :to="'/studio?id='+sid">{{title}}工作室</router-link>
              </el-breadcrumb-item>
              <el-breadcrumb-item key="3">
                资料列表
              </el-breadcrumb-item>
            </transition-group>
          </el-breadcrumb>
      </template>
    </breadcrumb-wrapper>

    <div class="tab-box flex-space-between w750">
      <el-radio-group v-model="activeName" size="small" @change='change'>
        <el-radio-button label="1">全部</el-radio-button>
        <el-radio-button label="2">我的</el-radio-button>
      </el-radio-group>
      <div class="Btn add-article"><router-link :to="{ path: '/addData' , query:{sid: sid,fromPage:'Database'}}">添加资料</router-link></div>

    </div>



    <div class="material-all material" v-show="activeName == 1">
      <div v-if="materiallists.length > 0">
        <el-card class="box-card" v-for="(item) in materiallists" :key="item.id">
          <div>
            <div class="w750">
              <div class="section-header flex-space-between">
                <div class="section-left h1">
                  <router-link class="ellipsis" :to="'/dataDetail/'+item.id+'?fromPage=Database'">
                    {{item.title}}
                  </router-link>

                </div>


              </div>
              <div class="section-main" v-html="item.brief">

              </div>
              <div class="section-footer">
                <span class="nick">{{item.creator.user_name}}</span>
                更新于
                <span class="updata-time"> {{item.mtime}}</span>


              </div>
            </div>

          </div>

        </el-card>

        <el-card class="box-card" shadow='hover' >
          <div class="more" @click="gomore">
            {{moreTxt}}
          </div>

        </el-card>
      </div>

        <div class="time " style="min-height: 550px;background: #ffffff;overflow: hidden" v-else>
          <tip txt="暂无内容" oder="3"></tip>
        </div>




    </div>


    <div class="material-my material" v-show="activeName == 2">

      <div v-if="materialmines.length > 0">
        <el-card class="box-card" v-for="(item) in materialmines" :key="item.id">
          <div class="section">
            <div class="w750">
              <div class="section-header flex-space-between">
                <div class="section-left h1">
                  <router-link class="ellipsis" :to="'/dataDetail/'+item.id+'?fromPage=Database'">
                    {{item.title}}

                  </router-link>


                </div>


              </div>
              <div class="section-main" v-html="item.brief">

              </div>
              <div class="section-footer" >
                <span class="nick time" >{{user_name}}</span>
                更新于
                <span class="updata-time"> {{item.mtime}}</span>


              </div>
            </div>

          </div>

        </el-card>

        <el-card class="box-card" shadow='hover'>
          <div class="more" @click="gomore">
            {{moreTxt}}
          </div>

        </el-card>

      </div>
      <div class="time " style="min-height: 550px;background: #ffffff;overflow: hidden" v-else>
        <tip txt="暂无内容" oder="3"></tip>
      </div>


    </div>





  </div>
</template>

<script>
import { mapGetters } from "vuex";
import BreadcrumbWrapper from '@/layout/BreadcrumbWrapper'
import {materiallist , materialmine , materialfind,subjectget} from '@/api/index'
import Tip from '@/components/Tip/index'
export default {
  name: "Database",
  components: {
        BreadcrumbWrapper,
    Tip
  },
  computed: {
    ...mapGetters(["name"])
  },
  data() {
    return {
      activeName: 1,
      materiallists:[],
      materialmines:[],
      sid:this.$route.query.id,
      fromPage:this.$route.query.fromPage,
      uid:this.$store.state.user.user.id,
      user_name:this.$store.state.user.user.user_name,
      limit: 1000,
      page: 1,
      page1: 1,
      moreTxt:'点击加载更多',
      key:'',
      flag: true,
      total:0,
      title:''

    };
  },
  created(){



  },
  mounted(){
    this.subjectGet()
    if(this.activeName == 1){
      this.materiallist()

    }else if(this.activeName == 2){

      this.materialmine()

    }
  },
  methods:{
    subjectGet() {
        subjectget({
          id: this.sid
        }).then((res) => {

          this.title = res.data.title
        })
    },
    change(){
      if(this.activeName == 1){
        this.page=1
        this.materiallist()

      }else if(this.activeName == 2){
        this.page=1

        this.materialmine()

      }
    },
    materiallist() {
      materiallist({
        sid: this.sid,
        limit:this.limit,
        page: this.page


      }).then((res) => {

        if(this.page > 1){ this.materiallists = this.materiallists.concat(res.data.list)}else {
          this.materiallists = res.data.list

        }

        this.total = res.data.total

        if(res.data.list.length<this.limit){
          this.flag = true
          this.moreTxt = '已无更多'

        }else{
          this.flag = false
        }
        this.page = ++ this.page



      })

    },
    materialmine() {
      materialmine({
        sid: this.sid,
        limit: this.limit,
        page: this.page
      }).then((res) => {
        if(this.page > 1){   this.materialmines = this.materialmines.concat(res.data.list)}else {
          this.materialmines = res.data.list

        }



        console.log('materialmines' , this.materialmines)




        this.total = res.data.total

        if(res.data.list.length<this.limit){
          this.flag = true
          this.moreTxt = '已无更多'
        }else{
          this.flag = false
        }
        this.page = ++ this.page



      })

    },


    gomore(){
      if(this.flag){
        if(this.key){

         /* if(this.activeName == 2){
            this.page1 = 1
          }*/

          this.materialfind()
        }else {
          if(this.activeName == 1){
            this.materiallist()
          }else {
            this.materialmine()
          }



        }

      }

    },
  }

};
</script>

<style lang="scss" scoped>
.section{

  background: #fff;
  padding: 0px 0px 10px 0px;

}


.section-footer{
  color: rgba(0, 0, 0, 0.447058823529412);
}
.section-footer .nick{
  color: #1890FF;
}
.section-footer .updata-time{
  font-size: 12px;
  color: rgba(0, 0, 0, 0.247058823529412);
  margin-left: 20px;
}
.section-main , .section-footer{
  margin-top: 15px

}
.section-main{
  font-size: 14px;
}
.section-footer{
  font-size: 12px !important;
}
  .material {
    margin-top: 20px;
  }
.more{
  text-align: center;
  cursor: pointer;
}


</style>



