<!--
 * @Description: 空提示组件，支持size参数:oder,txt参数,
 * oder:1  表示 未搜索到相关数据
  * oder:2  表示 网络不给力

  * oder:3  表示 暂无内容
    * oder:4  表示 暂无评论
    * oder:5  表示暂无通知
 * @Author: icony/李小发
 * @Date: 2019-06-21 18:22:11
 * @LastEditTime: 2019-07-09 14:38:23
 * @LastEditors: icony/精武陈真
 -->

<template>
  <div class="box">
    <div class="box-img">
      <img src="../../assets/images/norelateddata.png" v-if="oder== 1"/>
      <img src="../../assets/images/networkdoesnotforce.png" v-if="oder== 2"/>
      <img src="../../assets/images/nocontentyet.png" v-if="oder== 3"/>
      <img src="../../assets/images/nocommentyet.png" v-if="oder== 4"/>
      <img src="../../assets/images/nonotice.png" v-if="oder== 5"/>
    </div>

    <div class="box-txt">{{txt}}</div>
  </div>

</template>

<script>
  export default {
    name: 'Tip',
    props: {

      txt: {
        type: String,
        default: ''
      },
      oder: {
        type: String,
        default: ''
      },

    },

    computed: {}
  }
</script>
<style lang="scss" scoped>

  .box {
    width: 100%;
    height: 100%;
  }

  .box-img {

    width: 150px;
    height: 150px;
    margin: 0 auto;
    margin-top: 10%;

  }

  .box-txt {
    text-align: center;
    margin-top: 0px;
    color: #cccccc;

  }

</style>
